  height: 120px;
  margin: 0 auto;
}
.roulette .roulette-wrap:after {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  width: 500px;
  background: -webkit-linear-gradient(left, rgba(255, 255, 255, 0.001), #030c14);
  /* For Safari 5.1 to 6.0 */
  background: -o-linear-gradient(right, rgba(255, 255, 255, 0.001), #030c14);
  /* For Opera 11.1 to 12.0 */
  background: -moz-linear-gradient(right, rgba(255, 255, 255, 0.001), #030c14);
  /* For Firefox 3.6 to 15 */
  background: linear-gradient(to right, rgba(255, 255, 255, 0.001), #030c14);
  /* Standard syntax (must be last) */
  content: '';
}
.roulette .roulette-wrap:before {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 500px;
  background: -webkit-linear-gradient(right, rgba(255, 255, 255, 0.001), #030c14);
  /* For Safari 5.1 to 6.0 */
  background: -o-linear-gradient(left, rgba(255, 255, 255, 0.001), #030c14);
  /* For Opera 11.1 to 12.0 */
  background: -moz-linear-gradient(left, rgba(255, 255, 255, 0.001), #030c14);
